.request-field {
  width: 100%;
  min-height: 430px;
  display: flex;
  flex-direction: column;
  justify-content: center;
  color: #ffffff;
  background-color: #1b532d;
  background-image: url("../img/background.jpg");
  background-repeat: no-repeat;
  background-position: center top;
  background-size: cover;
  position: relative;
  overflow: hidden; }

.request-field::after {
  content: '';
  display: inline-block;
  width: 100%;
  height: 100%;
  position: absolute;
  top: 0;
  left: 0;
  background-color: rgba(0, 0, 0, 0.2); }

.request-field h2 {
  margin: 0;
  padding: 0;
  text-align: center;
  width: 50%;
  margin: 0 auto;
  font-weight: 300;
  line-height: 1.1;
  margin-bottom: calc(16px * 1.5 * 1.5);
  font-size: calc(16px * 2.5);
  position: relative;
  z-index: 5; }

.request-field form {
  box-sizing: border-box;
  max-width: 1280px;
  width: 780px;
  margin: 0 auto;
  padding: 0 30px;
  display: flex;
  justify-content: center;
  position: relative;
  z-index: 5; }

.request-field__input-wrapper {
  width: 100%;
  display: flex;
  position: relative; }

.request-field__input-wrapper::before {
  content: '';
  display: inline-block;
  width: 22px;
  height: 22px;
  position: absolute;
  top: 20px;
  left: 20px;
  background-image: url("../img/search.svg");
  background-position: center;
  background-repeat: no-repeat; }

.request-field input[type="text"] {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  width: 100%;
  min-height: 60px;
  border: none;
  border-radius: 3px;
  padding: 10px 55px;
  font-size: 20px;
  color: dimgray;
  outline: none;
  background-color: #ededed;
  transition: background-color 0.5s; }
  .request-field input[type="text"]:hover, .request-field input[type="text"]:focus {
    cursor: pointer;
    box-shadow: 0 0 2px 3px #3eae5f;
    background-color: #ffffff; }
  .request-field input[type="text"]:active {
    box-shadow: 0 0 2px 3px #3eae5f; }

.request-field button[type="submit"] {
  width: 100px;
  margin-left: 15px;
  border: none;
  border-radius: 3px;
  font-weight: bold;
  text-transform: uppercase;
  color: white;
  background-color: #3eae5f;
  outline: none;
  transition: background-color 0.5s; }
  .request-field button[type="submit"]:hover, .request-field button[type="submit"]:focus {
    cursor: pointer;
    background-color: #31884a; }
  .request-field button[type="submit"]:active {
    opacity: 0.4; }

@media (max-width: 1023px) {
  .request-field form {
    width: 100%;
    min-width: 100%;
    padding: 0 20px; } }

@media (max-width: 767px) {
  .request-field h2 {
    width: 90%; }
  .request-field__input-wrapper {
    flex-direction: column; }
  .request-field input[type="text"] {
    margin-bottom: 10px; }
  .request-field button[type=submit] {
    width: 100%;
    margin: 0;
    min-height: 60px; } }

